<template>
  <div id="app">
    <mu-appbar :title="headTitle">
      <mu-icon-button icon="menu" @click="toggle" slot="left"/>
      <mu-icon-menu icon="more_vert" slot="right">
        <mu-menu-item leftIcon="email" href="mailto:icefee@outlook.com?subject=" title="Mail to Me"/>
        <mu-menu-item leftIcon="phone" href="tel:18630954530" title="联系我"/>
      </mu-icon-menu>
    </mu-appbar>
    <mu-drawer :open="open" :docked="false" @close="toggle">
      <mu-list>
        <mu-list-item disabled>
          <mu-avatar :size="80" src="/static/img/avastar.jpg"/>
        </mu-list-item>
        <mu-divider />
        <mu-list-item to="/" title="Home">
          <mu-icon slot="left" value="home"/>
        </mu-list-item>
        <mu-list-item to="/list" title="Articles List">
          <mu-icon slot="left" value="star"/>
        </mu-list-item>
        <mu-list-item to="/about" title="About Me">
          <mu-icon slot="left" value="info"/>
        </mu-list-item>
      </mu-list>
    </mu-drawer>
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data() {
      return {
        open: false,
        headTitle: 'Home'
      }
    },
    methods: {
      toggle() {
        this.open = !this.open
      }
    },
    mounted() {
      this.headTitle = this.$route.name
    },
    watch: {
      '$route' (to, from) {
        this.headTitle = to.name
        this.open = false
      }
    }
  }
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
::-webkit-scrollbar {
  width: 4px;
}
.mu-item.show-left {
  padding-left: 60px;
}
</style>



