<template>
	<div class="articles">
		<mu-card v-for="item in articles" :key="item.id" to="/">
			<mu-card-title :title="item.title" :subTitle="item.subTitle"/>
			<mu-card-text>{{ item.text }}</mu-card-text>
			<mu-card-actions>
				<mu-raised-button label="Read More" :to="'/content/' + item.id" primary/>
			</mu-card-actions>
		</mu-card>
	</div>
</template>

<script>
	import http from '../http.js'

	export default {
		name: 'List',
		data() {
			return {
				articles: []
			}
		},
		created() {
			http.getJson('/static/data/articles.json', res => {
				if(res.status == 200) {
					this.articles = res.data.articles
				}
			}, err => {
				console.log(err)
			})
		}
	}
</script>

<style scoped>
	.articles {
		padding: 12px;
	}
	.articles .mu-card {
		margin-bottom: 12px;
	}
</style>


