<template>
	<div class="article" v-html="content"></div>
</template>

<script>
	import http from '../http.js'

	export default {
		name: 'Content',
		data() {
			return {
				content: ''
			}
		},
		created() {
			let id = this.$route.params.articleId;
			http.getJson('/static/data/' + id + '.json', res => {
				if(res.status == 200) {
					this.content = res.data.content
				}
			}, err => {
				console.log(err)
			})
		}
	}
</script>

<style scoped>
	.article {
		padding: 12px;
	}
</style>


